<template>
  <div class="wrapper">
    <v-loading loader="worker">
      <template slot="spinner">
        <pulse-loader :color="spinner.color" :height="spinner.height" :width="spinner.width"></pulse-loader>
      </template>

      <div class="animated fadeIn">
        <div class="row">
          <div class="col-12">
            <b-card header=" ">
              <b-btn v-b-modal.modalCreateUser href="#" variant="primary"> </b-btn>
              <br><br>

              <div class="row">
                <div class="col-md-3">
                  <b-form-group horizontal label=" " :label-cols="6">
                    <b-form-select :options="pageOptions" v-model="perPage" />
                  </b-form-group>
                </div>
                <div class="col-md-3">
                  <b-form-group horizontal label="" :label-cols="3">
                    <b-form-input v-model="filter" placeholder=" " />
                  </b-form-group>
                </div>
                <div class="col-md-2">
                  <b-button :disabled="!sortBy" @click="sortBy = null"></b-button>
                </div>
                <div class="col-md-4">
                  <b-pagination align="right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" />
                </div>
              </div>

              <b-table striped hover responsive show-empty
                :empty-text="messages.emptyText"
                :items="items"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :filter="filter"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                @filtered="onFiltered"
              >
                <template slot="actions" scope="row">
                  <b-btn variant="info" size="sm" @click.stop="details(row.item,row.index,$event.target)"></b-btn>
                  <b-btn variant="secondary" size="sm" @click.stop="modify(row.item,row.index,$event.target)"></b-btn>
                  <b-btn variant="danger" size="sm" @click.stop="remove(row.item,row.index,$event.target)"></b-btn>
                </template>
              </b-table>
            </b-card>
          </div>
        </div>
      </div>

      <!--     -->
      <b-modal id="modalShowInfo"
        @hide="resetModal"
      >
        <h4 slot="modal-header">Index: {{ modalDetails.index }}</h4>
        <pre>{{ modalDetails.data }}</pre>
      </b-modal>

      <!--    -->
      <b-modal id="modalCreateUser"
        :title="form.title"
        :header-bg-variant="modalVariants.headerBgVariant"
        :header-text-variant="modalVariants.headerTextVariant"
        :no-close-on-backdrop="true"
        :no-close-on-esc="true"
        @hide="initializeForm"
        hide-footer
      >
        <b-form @submit.prevent="onSubmit" novalidate>
          <b-form-group
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.role')"
          >
            <b-form-select id="userTypeInput" :options="userTypes"
              :state="!validation.firstError('form.role')"
              v-model="form.role"
              required placeholder=" "></b-form-select>
          </b-form-group>
          <b-form-group
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            v-if="displayShopSelect"
          >
            <v-select label="name"
              v-model="selectedShop"
              :options="shops"
              placeholder=" "
            ></v-select>
          </b-form-group>
          <b-form-group
            description=""
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.name')"
          >
            <b-form-input type="text"
              :state="!validation.firstError('form.name')"
              :autocomplete="'off'"
              v-model="form.name"
              required placeholder=" "></b-form-input>
          </b-form-group>
          <b-form-group
            description=""
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.phone')"
          >
            <b-form-input type="text"
              :state="!validation.hasError('form.phone')"
              :autocomplete="'off'"
              v-model="form.phone"
              required placeholder="  "></b-form-input>
          </b-form-group>
          <b-form-group
            description=""
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.email')"
          >
            <b-form-input type="email"
              :state="!validation.hasError('form.email')"
              :autocomplete="'off'"
              v-model="form.email"
              placeholder=" "></b-form-input>
          </b-form-group>
          <b-form-group
            description=""
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.password')"
            v-if="!form.id"
          >
            <b-form-input type="password"
              :state="!validation.hasError('form.password')"
              v-model="form.password"
              required placeholder=" "></b-form-input>
          </b-form-group>
          <b-form-group
            description=""
            label=" "
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.password_confirm')"
            v-if="!form.id"
          >
            <b-form-input type="password"
              :state="!validation.hasError('form.password_confirm')"
              v-model="form.password_confirm"
              required placeholder="  "></b-form-input>
          </b-form-group>
          <b-form-group
            description=""
            label=""
            :label-cols="modalLabelCols"
            :horizontal="modalInputHorizontal"
            :feedback="validation.firstError('form.memo')"
          >
            <b-form-textarea id="memoInput"
              :state="!validation.hasError('form.memo')"
              v-model="form.memo"
              placeholder=" "
              :rows="3"
              :max-rows="6">
            </b-form-textarea>
          </b-form-group>

          <b-container>
            <b-row>
              <b-col><b-btn block variant="primary" type="submit"></b-btn></b-col>
              <b-col><b-btn block variant="secondary" @click="initializeForm" type="reset"></b-btn></b-col>
              <b-col><b-btn block variant="danger" @click="hideModal"></b-btn></b-col>
            </b-row>
          </b-container>
        </b-form>
      </b-modal>
    </v-loading>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { Validator } from 'simple-vue-validator'

const fields = {
  role: { label: '', sortable: true, 'class': 'text-center' },
  shop_name: { label: '', sortable: true, 'class': 'text-center' },
  name: { label: '', sortable: true, 'class': 'text-center' },
  phone: { label: '', 'class': 'text-center' },
  email: { label: '' },
  actions: { label: '' }
}

export default {
  name: 'user',
  data () {
    return {
      selectedShop: null,
      fields: fields,
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      pageOptions: [
        { text: 5, value: 5 },
        { text: 10, value: 10 },
        { text: 15, value: 15 }
      ],
      sortBy: null,
      sortDesc: false,
      filter: null,
      modalDetails: { index: '', data: '' },
      form: null,
      modalLabelCols: 2,
      modalInputHorizontal: true
    }
  },

  validators: {
    'form.role' (value) {
      return Validator.custom(() => {
        if (Validator.isEmpty(value)) {
          return ''
        }
      })
    },
    'form.name' (value) {
      return Validator.value(value).required('')
    },
    'form.email' (value) {
      return Validator.value(value).email(' .')
    },
    'form.phone' (value) {
      return Validator.value(value).required('').digit(' ').length(11, '11 ')
    },
    'form.password' (value) {
      return Validator.value(value).required('').minLength(6, '6 ')
    },
    'form.password_confirm, form.password' (repeat, password) {
      if (this.validation.isTouched('form.password_confirm')) {
        return Validator.value(repeat).required('').match(password, ' ')
      }
    },
    'form.memo' (value) {
      return Validator.value(value).maxLength(40, '40 ')
    }
  },

  methods: {
    ...mapActions([
      'fetchUserLists',
      'fetchShopLists',
      'createUser',
      'updateUser',
      'deleteUser'
    ]),

    details (item, index, button) {
      this.modalDetails.data = JSON.stringify(item, null, 2)
      this.modalDetails.index = index
      this.$root.$emit('bv::show::modal', 'modalShowInfo', button)
    },

    modify (item, index, button) {
      this.form.title = ' '
      this.form.id = item.id
      this.form.role = item.role
      this.form.shop_id = item.shop_id
      this.form.shop_name = item.shop_name
      this.form.name = item.name
      this.form.phone = item.phone
      this.form.email = item.email
      this.form.memo = item.memo
      this.form.password = item.password ? item.password : ''
      this.form.password_confirm = this.form.password

      this.$root.$emit('bv::show::modal', 'modalCreateUser', button)
    },

    remove (item, index, button) {
      if (confirm(' ?')) {
        this.deleteUser(item.id)
      }
    },

    resetModal () {
      this.modalDetails.data = ''
      this.modalDetails.index = ''
    },

    hideModal () {
      this.initializeForm()
      this.$root.$emit('bv::hide::modal', 'modalCreateUser')
    },

    initializeForm () {
      this.form = Object.assign({}, this.form, {
        title: ' ',
        id: null,
        role: null,
        shop_id: null,
        shop_name: null,
        name: null,
        phone: null,
        email: null,
        password: null,
        password_confirm: null,
        memo: null,
        active: true
      })
    },

    onSubmit () {
      if (this.displayShopSelect) {
        if (this.selectedShop === null) {
          alert(' .')
          return false
        } else {
          this.form.shop_id = this.selectedShop.id
          this.form.shop_name = this.selectedShop.name
        }
      }

      this.$validate()
        .then(success => {
          if (success) {
            if (confirm('?')) {
              if (this.form.id) {
                this.updateUser(this.form)
              } else {
                this.createUser(this.form)
              }
              this.hideModal()
            }
          } else {
            alert('   .')
          }
        })
    },

    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },

  created () {
    this.fetchUserLists()
    this.fetchShopLists()
    this.initializeForm()
  },

  computed: {
    ...mapGetters({
      items: 'getAllUsers',
      shops: 'getAllShops',
      userTypes: 'userTypes',
      modalVariants: 'modalVariants',
      messages: 'messages',
      spinner: 'spinner'
    }),

    displayShopSelect () {
      return !(this.form.role === null || this.form.role !== 'shopkeeper')
    }
  }
}
</script>


